
import Search from 'vux/src/components/search/index.vue'

import searchTab from './searchTab'
import schoolList from './schoolList'
import { getAllSchool, comSearchApi } from '@/api/index.js'
export default {
components: { Search, searchTab, schoolList },
data () {
return {
curTab: '',
end: false,
loading: false,
key: '',
placeholder: '搜索',
list: [],
req: {
page: 0
},
page: 0
}
},
computed: {
openid () {
return this.$store.state.user.openid
},
listType () {
if (this.curTab === 'prov' || this.curTab === 'type') {
return 'one'
} else if (this.curTab === 'com') {
return 'two'
}
}
},
created () {
console.log('the hook of created is done!')
},
mounted () {
this.curTab = this.$route.params.type
this.clearQuery()
this.loading = true
this.getSchool()
this.$nextTick(() => {
this.$refs.list.init()
})
},
activated () {
if (this.list.length === 0) {
this.loading = true
this.getSchool()
}
},
watch: {
curTab () {
this.clearQuery()
}
},
methods: {
clearQuery () {
this.page = 0
this.list = []
this.end = false
this.req = {
page: 0
}
this.loading = true
},

gotoPage (name) {
this.clearQuery()
this.curTab = 'all'
this.$nextTick(() => {
this.$router.push({ name: name })
})
},

nextPage () {
this.page++
this.getSchool()
},

getSchool () {
if (this.curTab === 'all') {

this.getAllList()
} else {

this.getList()
}
},
getAllList () {
if (this.page === 0) {
this.end = false
this.list = []
}
getAllSchool({
openid: this.openid,
page: this.page
}).then(res => {
this.loading = false
if (res.data && res.data.data && res.data.data.length < 20) {
this.end = true
}
const schoolCode = this.list.map(item => item.schoolcode)

res.data.data.map(item => {
if (schoolCode.indexOf(item.schoolcode) === -1) {
this.list.push(item)
}
})
this.list = this.list.sort((a, b) => {
return Number(a.ranking) - Number(b.ranking)
})
console.log('学校列表信息：', this.list)
this.$nextTick(() => {
this.$refs.list.schoolScroll && this.$refs.list.schoolScroll.refresh()
})
})
},
getList () {
this.req.openid = this.openid
this.req.page = this.page
if (this.page === 0) {
this.end = false
}
comSearchApi(this.req).then(res => {
this.loading = false
if (res.data && res.data.data && res.data.data.length < 20) {
this.end = true
}
const schoolCode = this.list.map(item => item.schoolcode)
res.data.data.map(item => {
if (schoolCode.indexOf(item.schoolcode) === -1) {
this.list.push(item)
}
})
this.list = this.list.sort((a, b) => {
return Number(a.ranking) - Number(b.ranking)
})
this.$nextTick(() => {
this.$refs.list.schoolScroll && this.$refs.list.schoolScroll.refresh()
})
})
},

changeTab (tab) {
this.curTab = tab
this.clearQuery()
this.$router.push({ name: 'school', params: { type: tab } })
},

comSearch (obj) {
this.req = obj
this.list = []
this.req.openid = this.openid
this.req.page = 0
this.page = 0
this.loading = true
comSearchApi(obj).then(res => {
this.loading = false
const schoolCode = this.list.map(item => item.schoolcode)
console.log('xuexiao:', res.data)
res.data.data.map(item => {
if (schoolCode.indexOf(item.schoolcode) === -1) {
this.list.push(item)
}
})
this.$nextTick(() => {
this.$refs.list.schoolScroll && this.$refs.list.schoolScroll.refresh()
})
})
}
}
}



